import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { stringify } from 'querystring';


export class  Fermo  {
  
  idFermo : string;
  linea : string;
  idUtenteSitma : string;
  idUtentePoste : string;
  anomalia : string;
  grado : string;
  dataInizio: Date;
  turno : string;
  tipoTurno : string;
  modulo : string;
  sottoassieme : string;
  dataFine : Date;
  impattoDegrado : string;
  impactFactor : string;
  durataReale: string;
  durata : number;
  classeGuasto : string;
  tipoGuasto : string;
  causaGuasto : string;
  imputabilita : string;
  soluzione : string;
  descrSoluzione : string;
  dataValidazione : Date;
  refPoste : string;
  refAssistenza : string;
  deleted : boolean ;
  closed : boolean ;
  
  idUtentePosteNavigation: string;
  idUtenteSitmaNavigation:string;

}


@Injectable({
  providedIn: 'root'
})
export class FermiService {

  constructor(private http:HttpClient) {  
  }

  getFermi(){   

    return this.http.get(environment.apiUrl + "fermi/GetFermo");   
  }

  getFermo(id:string){   

    return  this.http.get(environment.apiUrl + "fermi/GetFermo/" + id); ;
  }

  deleteFermo(id:string){   

    return  this.http.delete(environment.apiUrl + "fermi/DeleteFermo/" + id); ;
  }

  postFermo(fermo:Fermo){      
    
    let bodyString = JSON.stringify(fermo)

    let headers = new HttpHeaders({ 'Content-Type': 'application/JSON' });

    return this.http.post<any>(environment.apiUrl + "fermi/PostFermo/", bodyString, { headers: headers })
     

  }
}
