import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { LoginComponent } from './components/login/login.component';
import { AboutComponent } from './components/about/about.component';
import { ContactComponent } from './components/contact/contact.component';
import { FermoManagementComponent } from './components/fermo-management/fermo-management.component';
import { FermoListComponent } from './components/fermo-list/fermo-list.component';


const routes = [
  {path: "", component: HomeComponent},
  {path: "home", component: HomeComponent}, 
  {path: "about", component: AboutComponent},
  {path: "contact", component: ContactComponent},
  {path: "login", component:LoginComponent},
  {path: "fermo-management", component:FermoManagementComponent},
  {path: "fermo-list", component:FermoListComponent},


   // otherwise redirect to home
  { path: '**', redirectTo: '' }
]




@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }


