import { Component, OnInit, Output, EventEmitter} from '@angular/core';
import { FormGroup, FormBuilder, angular_packages_forms_forms_w } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { LoginService, ILoginResult } from 'src/app/services/login.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent {

    loginForm: FormGroup;
    error: boolean;
    errorMsg: string

    constructor(private router: Router, private loginService: LoginService) {
    }

  // convenience getter for easy access to form fields
   get f() { return this.loginForm.controls; }

  @Output()
  login = new EventEmitter<{ username: string, password: string }>()

  doLogin(username: string, password: string) {
    delete this.error
    if (!username || !password){
      this.error = true
      this.errorMsg =" Per favore inserisci uno username e una password validi"
      return false
    }
    else
    {
      let loginResultHandler = (result: ILoginResult) =>
      {
        if(result.success)
        {
          this.router.navigateByUrl("fermo-list")
        }
        else
        {
          this.error = true
          this.errorMsg =" Username e/o password  non validi"
        }
      }

      this.loginService.login(username, password, loginResultHandler)
      //this.login.emit({username, password})
    }       
  } 
}
