import { Component, OnInit, Input } from '@angular/core';
import { FermiService } from 'src/app/services/fermi.service';
import { PaginationInstance } from 'ngx-pagination/dist/ngx-pagination.module';
import { Router, ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-fermo-list',
  templateUrl: './fermo-list.component.html',
  styleUrls: ['./fermo-list.component.css']
})
export class FermoListComponent implements OnInit {

  action: string
  idFermo: string
  currentUserLoggedIn: any
  notAdmin: boolean
 

  @Input('data') fermi: any[]
  p: number = 1;
  
  public filter: string = '';
  public maxSize: number = 7;
  public directionLinks: boolean = true;
  public autoHide: boolean = false;
  public responsive: boolean = false;
  public config: PaginationInstance = {
      id: 'advanced',
      itemsPerPage: 10,
      currentPage: 1
  };
  public labels: any = {
      previousLabel: 'Previous',
      nextLabel: 'Next',
      screenReaderPaginationLabel: 'Pagination',
      screenReaderPageLabel: 'page',
      screenReaderCurrentLabel: `You're on page`
  };
  public eventLog: string[] = [];

  private popped = [];

  onPageChange(number: number) {
    this.logEvent(`pageChange(${number})`);
    this.config.currentPage = number;
}

onPageBoundsCorrection(number: number) {
    this.logEvent(`pageBoundsCorrection(${number})`);
    this.config.currentPage = number;
}

pushItem() {
    let item = this.popped.pop() || 'A newly-created meal!';
    this.fermi.push(item);
}

popItem() {
    this.popped.push(this.fermi.pop());
}

private logEvent(message: string) {
    this.eventLog.unshift(`${new Date().toISOString()}: ${message}`)
}
  
  constructor( private api: FermiService,  private router: Router, private routed:ActivatedRoute) {

    this.currentUserLoggedIn =  JSON.parse(localStorage.getItem('currentUser'));

   }

  
  ngOnInit() {

    this.notAdmin = true

    for (let idUtenteRuolo of  this.currentUserLoggedIn.utenteRuolo )
    {
       if(idUtenteRuolo.idRuoloNavigation.nomeRuolo == 'AMMINISTRATORE')     
       {
        this.notAdmin = false
         break;
       }
     
    }

    this.routed.queryParams.subscribe(params => {

      console.log(params)

      this.action = params.action;
      this.idFermo = params.idfermo;     
      
      switch (this.action) {  
        case "delete":  
         this.deleteFermo();  
         break;         
        default:          
       }  

    })

   this.loadPage()

  }   

  loadPage()
  {
    const onSuccess = (res) => {
      console.log(res)
      this.fermi = res
    }
    const onError = (err) => {
      console.log(err)
    }
     this.api.getFermi().subscribe(onSuccess, onError)
  }

  deleteFermo()
  {
      this.api.deleteFermo(this.idFermo).subscribe(
      (res) => {
        console.log(res)
         this.loadPage()
      }      ,
      (err) => {
        console.log(err)
        this.router.navigateByUrl("fermo-list")
      }
    );     
  }


}
