import {Injectable} from '@angular/core';
import {HttpClient, HttpParams, HttpHeaders} from "@angular/common/http";
import { BehaviorSubject, Observable } from 'rxjs';
import { escapeRegExp } from '@angular/compiler/src/util';
import { environment } from 'src/environments/environment';
import { Utente } from '../_models/utente';

@Injectable({
  providedIn: 'root'
})
export class LoginService {

 
  loginUrl =    environment.apiUrl + "utenti/GetToken"; 
  private TOKEN_KEY = "TOKEN_KEY" //pu essere qualunque stringa

  private loggedIn = new BehaviorSubject<boolean>(false);
  //private isLoggedIn$ = this.loggedIn.asObservable();
  private isLoginSubject = new BehaviorSubject<boolean>(false);

  constructor(private http: HttpClient) {
  }

  login(email: string, password: string, handler: (result: ILoginResult) => void) {
    
    
    /***********prova   
    var myToken = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJoZWxsbyI6IndvcmxkIn0.lnneNaoem98xYFES3mi2CJJjnMONuWAu-FTWB3XJN14";

    localStorage.setItem(this.TOKEN_KEY, myToken)
  
    handler({success: true, error: false, token: myToken})

    this.isLoginSubject.next(true);

    /******** fine prova */    

    let bodyString = JSON.stringify({Email: email, Password: password})

    let headers = new HttpHeaders({ 'Content-Type': 'application/JSON' });

    this.http.post<any>(this.loginUrl, bodyString, { headers: headers }).subscribe((res: any) => {
      if (res.token) {

        console.log(res)       
        console.log(res.token)       

        localStorage.setItem(this.TOKEN_KEY, res.token)
        localStorage.setItem('currentUser', JSON.stringify(res));

        this.isLoginSubject.next(true);
       
      }
      handler({success: true, error: false, token: res.token})
    }, err => {
      console.log(err)
      this.isLoginSubject.next(false);
      handler({success: false, error: true})
    })

  }

 

  get tokenData() {
    let token = localStorage.getItem(this.TOKEN_KEY)
    if (token) {
      return JSON.parse(atob(token.split(".")[1]))
    }
    return {}
  }

  isLogged() {
    let token = localStorage.getItem(this.TOKEN_KEY)
    if (!token)
    { 
      this.isLoginSubject.next(false);
      return false
    }
    else
    {
      this.isLoginSubject.next(true);
      let secondToExpire = (this.tokenData.exp - new Date().getTime()) * 1000
      return secondToExpire > 1
    }   
  }

  logout() {
    this.isLoginSubject.next(false);
    localStorage.removeItem(this.TOKEN_KEY)
    localStorage.removeItem("currentUser")
  }


isLoggedIn() : Observable<boolean> {
  this.isLogged()
  return this.isLoginSubject.asObservable();
 }


}

export interface ILoginResult {
  success: boolean,
  error: boolean
  token?: string
}